import * as React from 'react';
import EditableTextline from "../../../../core/components/EditableTextline";
import {useGraph} from "../../../hooks/useGraph";
import {makeNodeFromJs, Node, NodeType} from "../../../model/Graph";

interface OwnProps {
  nameSuggestion?: string;
  nodeType: NodeType;
  onEntered: (newNode: Node) => void;
  onCancel: () => void;
}

type NewNodeInputProps = OwnProps;

const NewNodeInput = (props: NewNodeInputProps) => {

  const [graph,] = useGraph();

  const handleTextChanged = (name: string) => {
    const newNode = makeNodeFromJs({
      id: graph.createCellId(),
      name,
      type: props.nodeType
    });

    props.onEntered(newNode);
  }

  return <EditableTextline text={''} textSuggestion={props.nameSuggestion || ''} required={true} charSize={1} fixedWidth={true} widthUnit={'%'} minWidth={100} onTextChanged={handleTextChanged} focus={true} onCancel={props.onCancel} />
};

export default NewNodeInput;
