import {WizardStep} from "../../../components/CodyEngineWizard/WizardModal";
import {names} from "../node/names";

export const stepHeader = (step: WizardStep): string => {
  switch (step) {
    case "state":
      return 'Information';
    case "aggregateEvent":
      return 'Domain Event';
    case "publicEvent":
      return 'Public Domain Event';
    case "policyEvent":
      return 'Domain Event';
    case "anyVO":
      return 'Information';
    case "runCody":
      return 'Run Cody';
    default:
      return names(step).className;
  }
}
